.new-minecraft{
    .btn_registration {
        width: 272px;
        height: 44px;
        box-shadow: 0px 4px 2px rgba(10, 15, 6, 0.3);
        border-radius: 4px;
        border-top: 4px solid $color-nm-lite-green;
        border-bottom: 4px solid $color-nm-deep-green;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $color-nm-green;
        transition: background-color 750ms, border 750px;
        cursor: pointer;
        text-transform: uppercase;
        color: $color-white;
        font-size: 16px;
        line-height: 1.25;
        &:hover,
        &:focus{
            background-color: $color-nm-btn-act;
            border-top: 4px solid $color-nm-lite-green;
            border-bottom: 4px solid $color-nm-deep-green;
        }
        @media screen and (min-width: 768px) {
            width: 280px;
            height: 54px;
          }
}
}